const Cookie = process.client ? require('js-cookie'): undefined
import { updateUser } from '@/api/user'
import { mapState } from 'vuex'

export default {
    name: 'SettingIndex',
    middleware: 'auth',
    data() {
        return {
            author: {
              username: '',
              email: '',
              bio: '',
              image: '',
              password: ''
            },
            disabled: false,
            errors: {}
        }
    },
    computed: {
      ...mapState(['user'])
    },
    mounted () {
        this.author.bio = this.user.bio
        this.author.email = this.user.email
        this.author.image = this.user.image
        this.author.password = this.user.password
        this.author.username = this.user.username
    },
    methods: {
        async onSubmit () {
            this.disabled = true
            try {
                const { data } = await updateUser({
                  user: this.author
                })

                // 成功后跳转到该文章页面 /article/slug
                // 更新用户的登录状态
                this.$store.commit('setUser', data.user)
                // 为了防止刷新页面数据丢失，数据需要持久化
                Cookie.set('user', data.user)

                this.$router.push(`/profile/${data.user.username}`)
                // const { slug } = data
                // this.$router.push({
                //   name: 'article',
                //   params: {
                //     slug
                //   }
                // })

            } catch (err) {
                this.disabled = false
                this.errors = err.response.data.errors
            }
        },
        logout () {
            // 删除用户的登录状态
            this.$store.commit('setUser', null)

            // 删除数据持久化
            Cookie.set('user', null)

            this.$router.push('/')
        }
    }
}
